import {authAPI, login, logout} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'SET_USER_DATA'


let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {...state, ...action.data}
        default: return state
    }
}

export const setUserData = (userId, email, login, isAuth = false) => (
    {type: SET_USER_DATA, data: {userId, email, login, isAuth}}
)

export const authThunkCreator = () => {
    return async dispatch => {
        return (async () => {
            let result = false
            const resp = await authAPI()
            if (resp.data.resultCode === 0) {
                result = true
                let {id, login, email} = resp.data.data
                dispatch(setUserData(id, email, login, true))
            }
            return result
        })()
    }
}

export const loginSubmit = (email, password, rememberMe) => async dispatch => {
    const loginResp = await login(email, password, rememberMe)
    if (loginResp.data.resultCode === 0) {
        dispatch(authThunkCreator())
    } else {
        dispatch(stopSubmit('Login', {_error: 'Какая - то общая ошибка'}))
    }
}

export const LogoutThunkCreator = () => async dispatch => {
    const loginResp = await logout()
    if (loginResp.data.resultCode === 0) {
        dispatch(setUserData(null, null, null))
    }
}

export default authReducer