import React from "react";
import s from './style.module.css'

const InputErrors = ({input, meta: {touched, error}, children, ...props}) => {
    const err = error && touched
    return (<div className={s.inputWrapper}>
        {children}
        <div>
            {err && <span>{error}</span>}
        </div>
    </div>)
}

export const TextArea = props => {
    const {input, meta, ...restProps} = props
    return <InputErrors {...props}> <textarea {...input} {...restProps}/></InputErrors>
}

export const InputArea = props => {
    const {input, meta, ...restProps} = props
    return <InputErrors {...props}> <input {...input} {...restProps}/></InputErrors>
}