import React from "react";
import fh_logo from "../../assets/logo.png";
import cls from './Header.module.css'
import {NavLink} from "react-router-dom";

const Header = (props) => {
    return <header className = {cls.header}>
        <img src = {fh_logo}/>
        <div className={cls.loginBlock} onClick={props.logout}>
            {props.isAuth
                ? props.login
                : <NavLink to={'/login'}>Залогиниться</NavLink>}
        </div>
    </header>
}

export default Header;