import {getDate} from "../tools/tools";

const SEND_MESSAGE = 'SEND_MESSAGE';

export const messageSendActionCreator = (newValue) => {
    return {
        type: SEND_MESSAGE,
        newValue
    }
}

const initialState = {
    dialogs: [
        {id: 1, name: 'Лапузин Андрей'},
        {id: 2, name: 'Семенова Анна'},
        {id: 3, name: 'Пыжьянов Дмитрий'}
    ],
    messages: [
        {
            messageDate: '20 мая 1994г 18:00:00',
            text: 'Эй, чувак, здарова! Смотри, какое классное сообщение я тебе написал'
        },
        {
            messageDate: '20 мая 1994г 18:00:01',
            text: 'Ваааааау!!! Этого не может быть, это так идеально!!!',
            my: 'true'
        }
    ]
}

const dialogsReducer = (state = initialState, action) => {
    const type = action.type
    let newState
    switch (type) {
        case SEND_MESSAGE:
             if (action.newValue) {
                newState = {
                    ...state,
                    messages: [...state.messages, {messageDate: getDate(), text: action.newValue, my: true}]
                }
                return newState
        } else {
                return state
            }
        default:
            return state
    }
}

export default dialogsReducer