import {getDate} from "../tools/tools";
import {getProfile, getStatus, updateStatus} from "../api/api";

const ADD_POST = 'ADD_POST'
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS = 'SET_STATUS'
const DELETE_POST = 'DELETE_POST'

export const addPostActionCreator = (text) => {
    return {
        type: ADD_POST,
        text: text,
    }
}

export const deletePostActionCreator = id => ({id, type: DELETE_POST})

export const setStatus = status => {
    return {
        type: SET_STATUS,
        status: status
    }
}

export const setUserProfile = id => {
    return async dispatch => {
        const userData = await getProfile(id)
        const status = await getStatus(id)
        dispatch({type: SET_USER_PROFILE, data: {userData, status}})
    }
}

export const updStatus = status => {
    return async dispatch => {
        const result = await updateStatus(status)
        if (result.resultCode === 0) {
            console.log('Статус обновлен на ' + status)
            dispatch(setStatus(status))
        }
    }
}

let _lastId = 3

const initialState = {
    profile: null,
    posts: [
        {
            date: '20 мая 1994г 18:00:00',
            text: "Эй, чувак, смотри какие крутые посты я теперь могу писать, передавая сообщения через props",
            id: 1
        },
        {
            date: '20 мая 1994г 18:00:01',
            text: "Здарова, жирдяй!",
            id: 2
        }
    ],
    newPostText: ''
}

const profileReducer = (state = initialState, action) => {
    const type = action.type
    switch (type) {
        case ADD_POST:
            if (action.text) {
                return {
                    ...state,
                    posts: [{text: action.text, date: getDate(), id: _lastId++}, ...state.posts],
                }
            } else return state
        case SET_USER_PROFILE:
            return {...state, profile: action.data.userData, status: action.data.status}
        case SET_STATUS:
            return {...state, status: action.status}
        case DELETE_POST: {
            return {...state, posts: state.posts.filter(post => post.id !== action.id)}
        }
        default:
            return state
    }
}

export default profileReducer