import React from "react";
import usersLine from "../usersline.module.css";
import {NavLink} from "react-router-dom";

const User = (props) => {
    return (
        <NavLink className={usersLine.userWrapper}
                 to={"/dialogs/" + props.id} activeClassName={usersLine.active}>
            <div className={usersLine.user}>
                <img className={usersLine.roundImage}
                     src='https://seeklogo.com/images/F/fat-man-colectivo-logo-267CCD18AC-seeklogo.com.jpg'>
                </img>
                <span className={usersLine.username}>{props.name}</span>
            </div>
        </NavLink>
    )
}

export default User