import {UserAPI} from "../api/api";
import {updateObjectInArray} from "../tools/objects-handler";

const FOLLOW = 'FOLLOW'
const UNFOLLOW = 'UNFOLLOW'
const SETUSERS = 'SETUSERS'
const SETCURRENTPAGE = 'SETCURRENTPAGE'
const SETUSERSCOUNT = 'SETUSERSCOUNT'
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING'
const FOLLOW_IN_PROGRESS = 'FOLLOW_IN_PROGRESS'

let defaultAvatarPhoto = 'http://beauty73.org/wp-content/uploads/2018/11/92979836-profile-anonymous-face-icon-gray-silhouette-person-male-default-avatar-photo-placeholder-isolated-on-300x300.jpg'

let initialState = {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true
}

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state, users: updateObjectInArray(state.users, action.userId, 'id', {followed: true})
            }
        case UNFOLLOW:
            return {
                ...state, users: updateObjectInArray(state.users, action.userId, 'id', {followed: false})
            }
        case SETUSERS:
            return {...state, users: [...setUsersToStore(action.users)]}
        case SETCURRENTPAGE:
            return {...state, currentPage: action.currentPage}
        case SETUSERSCOUNT:
            return {...state, totalUsersCount: action.usersCount}
        case TOGGLE_IS_FETCHING:
            return {...state, isFetching: action.fetching}
        case FOLLOW_IN_PROGRESS:
            return {
                ...state, users: state.users.map(user => {
                    if (user.id === action.id) {
                        return {...user, inProgress: action.inProgress}
                    } else {
                        return user
                    }
                })
            }
        default:
            return {...state}
    }
}

const setUsersToStore = users => users.map(user => ({
    id: user.id,
    name: user.name,
    photo: user.photos.small || defaultAvatarPhoto,
    followed: user.followed,
    status: user.status || 'Обычный пользователь',
    location: user.location || {country: 'Страна не указана', city: 'Город не указан'},
    inProgress: false
}))

export const follow = userId => ({type: FOLLOW, userId})
export const unfollow = userId => ({type: UNFOLLOW, userId})
export const setUsers = users => ({type: SETUSERS, users})
export const setCurrentPage = currentPage => ({type: SETCURRENTPAGE, currentPage})
export const setUsersCount = usersCount => ({type: SETUSERSCOUNT, usersCount})
export const toggleIsFetching = fetching => ({type: TOGGLE_IS_FETCHING, fetching})
export const followInProgress = (id, inProgress) => ({type: FOLLOW_IN_PROGRESS, inProgress, id})

// thunk
export const getUsersThunkCreator = (pageSize, pageNumber, totalUsersCount) => {
    return async (dispatch) => {
        dispatch(setCurrentPage(pageNumber))
        dispatch(toggleIsFetching(true))

        const users = await UserAPI.getUsersResp(pageSize, pageNumber)

        dispatch(toggleIsFetching(false))
        dispatch(setUsers(users.items))

        if (totalUsersCount === 0) {
            dispatch(setUsersCount(users.totalCount))
        }
    }
}

export const followThunkActionCreator = (id, method, followed) => {
    return async dispatch => {
        dispatch(followInProgress(id, true))
        const result = await UserAPI.followHandler(id, method)
        dispatch(followInProgress(id, false))
        if (result === 0) {
            dispatch((followed ? unfollow(id) : follow(id)))
        }
    }
}

export default usersReducer