import {authThunkCreator} from "./auth-reducer";

const SET_INITIALIZED = 'SET_INITIALIZED'

const initialState = {
    initialized: false
}

export const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_INITIALIZED:
            return {...state, initialized: action.init}
        default: return state
    }
}

export const setInitialized = init => ({type: SET_INITIALIZED, init})

export const initializeThunk = () => async dispatch => {
    const dispatchResult = await dispatch(authThunkCreator())
    dispatch(setInitialized(true))

}