import React, {Suspense} from "react";
import './App.css'
import Nav from "./components/Navbar/Nav";
import Footer from "./components/Footer/Footer";
import {BrowserRouter, Route, withRouter} from "react-router-dom"
import HeaderContainer from "./components/Header/HeaderContainer";
import Login from "./components/Login/Login";
import {connect, Provider} from "react-redux";
import {compose} from "redux";
import {initializeThunk} from "./redux/app-reducer";
import Burger from "./components/AssetComponents/Loaders/burger-loader";
import store from "./redux/redux-store";
import WithSuspenseComponent from "./HOC/WithSuspenseHoc";

const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));
const UsersAPI = React.lazy(() => import('./components/FindFriends/usersContainer'));

class App extends React.Component {

    componentDidMount() {
        this.props.authorize()
    }

    render() {
        if (this.props.init) {
            return (
                <div className='AppWrapper'>
                    <HeaderContainer/>
                    <Nav/>
                    <div className="AppWrapperContent">
                        <Route path="/profile/:userId?" render={
                            WithSuspenseComponent(ProfileContainer)
                        }/>
                        <Route path="/dialogs" render={
                            WithSuspenseComponent(DialogsContainer)
                        }/>
                        <Route path="/users" render={
                            WithSuspenseComponent(UsersAPI)
                        }/>
                        <Route path='/login' render={() => <Login/>}/>
                    </div>
                    <Footer/>
                </div>
            )
        } else {
            return <Burger/>
        }
    }
}

const mapStateToProps = (state) => ({
    init: state.app.initialized
})

const AppContainer = compose(withRouter, connect(mapStateToProps, {authorize: initializeThunk}))(App);

export const FatHubApp = props => {
    return (
        <BrowserRouter>
            <Provider store={store}>
                <AppContainer/>
            </Provider>
        </BrowserRouter>)
}
