import React from "react";
import {Field, reduxForm} from "redux-form";
import {InputArea} from "../AssetComponents/FormsControls/FormsControls";
import {maxLengthCretaor, requiredField} from "../../tools/validator";
import {connect} from "react-redux";
import {loginSubmit, LogoutThunkCreator} from "../../redux/auth-reducer";
import {Redirect} from "react-router-dom";
import style from '../AssetComponents/FormsControls/style.module.css'

const maxLength = maxLengthCretaor(64)

const Login = ({loginSubmit, isAuth}) => {
    const onSubmit = formData => loginSubmit(formData.email, formData.password, formData.remember)

    if (isAuth) {
        return <Redirect to = '/profile'/>
    }

    return (
        <div>
            <h1>Login</h1>
            <ReduxLoginForm onSubmit = {onSubmit}/>
        </div>
    )
}

const LoginForm = ({handleSubmit, error}) => {
    return (<form onSubmit={handleSubmit}>
        <div>
            <Field type="text" placeholder={"email"} name = {"email"} component={InputArea} validate={[requiredField, maxLength]}/>
        </div>
        <div>
            <Field type="password" placeholder={"password"} name = {"password"} component={InputArea} validate={[requiredField, maxLength]}/>
        </div>
        <div>
            <Field type="checkbox" component={InputArea} name={"remember"}/>Запомнить меня
        </div>
        {error && <div className={style.summaryError}>{error}</div>}
        <div>
            <button>Войти</button>
        </div>
    </form>)
}

const ReduxLoginForm = reduxForm({
    form: 'Login'
})(LoginForm)

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {loginSubmit, LogoutThunkCreator})(Login)
