import axios from "axios";

const instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {"API-KEY": "65ddfa77-da24-4131-a6d0-90b3326f18f6"}
})

export const login = async (email, password, rememberMe = false) => {
    return (await instance.post('auth/login', {email, password, rememberMe}))
}

export const logout = async () => {
    return (await instance.delete('auth/login'))
}

export const authAPI = async () => {
    return (await instance.get('auth/me'))
}


export const UserAPI = {
    getUsersResp: async (pageSize = 10, currentPage = 1) => {
        const resp = await instance.get(`users?page=${currentPage}&count=${pageSize}`)
        return resp.data
    },

    followHandler: async (userId, method) => {
        const resp = await instance[method](
            `follow/${userId}`, method === 'post' && {})

        return resp.data.resultCode
    }
}

export const getProfile = async id => {
    return (await instance.get(`profile/${id}`)).data
}

export const getStatus = async id => {
    return (await instance.get(`profile/status/${id}`)).data
}

export const updateStatus = async status => {
    return (await instance.put(`profile/status`, {status})).data
}
