import UsersLine from "./usersLine";
import User from "./user/User";
import {connect} from "react-redux";

const mapStateToProps = (state) => {
    const users = state.topUsers && state.topUsers.map(user => <User id = {user.key} name = {user.name} key = {user.key}/>)
    return {
        users
    }
}

const mapDispatchToProps = () => {
    return {}
}

const UsersLineContainer = connect(mapStateToProps, mapDispatchToProps)(UsersLine)

export default UsersLineContainer